.pageBg {
	background: var(--bg-minibox);
}

.balanceBox {
	border-radius: 4px;
	padding: 16px;
	margin: 16px 0 32px 0;
	background: var(--bg-white);
}

.balanceAmount {
	color: #2EC193;
	font-weight: 500;
}

.numberValue {
	font-weight: 500;
	padding-right: 10px;
}

.otherDetails {
	border-radius: 4px;
	padding: 8px 16px;
	margin: 16px 0 32px 0;
	border: 1px solid var(--border-color-1);
	background: var(--bg-white);
}

.otherDetailsInfo {
	display: flex;
	align-items: center;
	padding: 8px 0;
}

.otherDetailsInfo1 {
	padding: 8px 0;
}

.otherDetailsInfoLabel {
	width: 50%;
}

.otherDetailsInfoValue {
	width: 50%;
	text-transform: capitalize;
}

.otherDetailsInfoValue1 {
	width: 50%;
}

.statement {
	border: 1px solid var(--border-color-1);
	border-radius: 4px;
	cursor: pointer;
	padding: 16px;
	justify-content: space-between;
	margin-bottom: 12px;
	background: var(--bg-white);
}

.location__map {
	background-color: rgb(219, 219, 219);
	border-radius: 4px;
	height: 170px;
}